.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  margin: auto;
  overflow: hidden;

  &__content {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      to left,
      var(--color-primary),
      var(--color-primary-3)
    );
    @include respond(tab-land) {
      font-size: 1.7rem;
      background: linear-gradient(
        to left,
        var(--color-primary),
        var(--color-dark-2)
      );
    }
    opacity: 0; /* Start invisible */
    animation: FadeIn 3s forwards; /* 3s fade-in animation */
    pointer-events: none; /* Allows interaction with the video */
  }
}
